import {
  getUserList,
  deleteUserInfo
} from "../../../../../utils/employment.url";
import userDialog from "./commonModel/userDialog.vue";
export default {
  components: {
    userDialog
  },
  data() {
    return {
      input: '',
      userList: [], //用户列表
      search: '',
      pageObj: {
        pageRowNum: 8,
        currPage: 1,
        totalRow: 20,
      },
      dialogFormVisible: false,
      editUserInfo: {},
    }
  },
  methods: {
    getUserList(pageObj) {
      getUserList(pageObj, "get").then(res => {
        if (res.status == 200) {
          this.userList = res.data.userList;
          this.pageObj = res.data.pageObj;
        }
      })
    },
    changePage(val) {
      console.log(val)
      this.pageObj.currPage = val;
      this.getUserList(this.pageObj);
    },
    dialogClose(value) {
      this.$store.commit('changeUserStatus', false);
      this.dialogFormVisible = value;
      this.getUserList(this.pageObj);
    },
    handleEdit(index, row) {
      // console.log(row)
      this.editUserInfo = row;
      this.dialogFormVisible = true;
      this.$store.commit('changeUserStatus', true);
    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteUserInfo(row, "delete").then(res => {
          if (res.status == 200) {
            this.getUserList(this.pageObj);
            this.$message({
              message: res.message,
              type: "success"
            });
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    }
  },
  created() {
    this.getUserList(this.pageObj);
  }
}