<template>
  <div class="userDialogBox">
    <el-dialog :title="this.$store.getters.getUserFlag ? '修改用户信息' : '新增新用户'"
               :visible="dialogFormVisible"
               v-if="dialogFormVisible"
               width="40%"
               @close="dialogClose">
      <el-form :model="userForm"
               :rules="rules"
               ref="userForm">
        <el-form-item label="学号/教工号"
                      :label-width="formLabelWidth"
                      prop="userId">
          <el-input v-model="userForm.userId"
                    autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名"
                      :label-width="formLabelWidth"
                      prop="userName">
          <el-input v-model="userForm.userName"
                    autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码"
                      :label-width="formLabelWidth"
                      prop="password">
          <el-input v-model="userForm.password"
                    autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="用户类型"
                      :label-width="formLabelWidth"
                      prop="userType">
          <el-switch v-model="userForm.userType"
                     active-text="辅导员"
                     inactive-text="学生"></el-switch>
        </el-form-item>
        <el-form-item label="所属年份"
                      :label-width="formLabelWidth"
                      prop="session">
          <el-date-picker v-model="userForm.session"
                          type="year"
                          placeholder="选择所属年级"
                          value-format="yyyy"> </el-date-picker>
        </el-form-item>
        <el-form-item label="图片"
                      :label-width="formLabelWidth">
          <el-upload class="upload-demo"
                     ref="upload"
                     action="#"
                     accept="image/png, image/gif, image/jpg, image/jpeg"
                     :file-list="fileList"
                     :auto-upload="false"
                     :before-upload="handleBeforeUpload"
                     :on-change="fileChange">
            <el-button slot="trigger"
                       size="small"
                       type="primary">选取图片</el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="cancelEdit">取消</el-button>
        <el-button type="primary"
                   @click="submitForm('userForm')">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { addNewUserInfo, updateUserInfo } from "../../../../../../utils/employment.url";
import { isNum } from "@/utils/utils";
export default {
  props: ["dialogFormVisible", "userInfo", "userForm"],
  data () {
    return {
      // presentTitle:"添加就业代表信息",
      formLabelWidth: "120px",
      fileList: [],
      rules: {
        userId: [{ required: true, message: "请输入学号/教工号", trigger: "blur" }],
        userName: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" }
        ],
        userType: [
          { required: true, message: "请选择用户类型", trigger: "blur" }
        ],
        session: [{ required: true, message: "请选择用户所属年份", trigger: "blur" }]
      }
    };
  },
  methods: {
    dialogClose () {
      this.$emit("dialogClose", false);
    },
    //,取消
    cancelEdit () {
      this.$emit("dialogClose", false);
    },
    // 上传文件之前的钩子
    handleBeforeUpload (file) {
      console.log("before");
      if (
        !(
          file.type === "image/png" ||
          file.type === "image/gif" ||
          file.type === "image/jpg" ||
          file.type === "image/jpeg"
        )
      ) {
        this.$notify.warning({
          title: "警告",
          message:
            "请上传格式为image/png, image/gif, image/jpg, image/jpeg的图片"
        });
      }
      let size = file.size / 1024 / 1024 / 2;
      if (size > 2) {
        this.$notify.warning({
          title: "警告",
          message: "图片大小必须小于2M"
        });
      }
    },
    // 文件状态改变时的钩子
    fileChange (file, fileList) {
      this.userForm.avatar = file.raw;
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let formData = new FormData();
          formData.append("avatar", this.userForm.avatar);
          formData.append("userName", this.userForm.userName);
          formData.append("password", this.userForm.password);
          formData.append("userId", this.userForm.userId);
          formData.append("userType", this.userForm.userType?2:1);
          formData.append("session", this.userForm.session);
          if (this.$store.getters.getUserFlag) {
            formData.append("id", this.userForm.id);
            updateUserInfo(formData, "put").then(res => {
              if (res.status == 200) {
                this.$message({
                  message: res.message,
                  type: "success"
                });
                this.$emit("dialogClose", false);
              }
            });
          } else {
            addNewUserInfo(formData, "post").then(res => {
              if (res.status == 200) {
                this.$message({
                  message: res.message,
                  type: "success"
                });
                this.$emit("dialogClose", false);
              }
            });
          }

        } else {
          return false;
        }
      });
    }
  },
  created () { }
};
</script>
<style lang="scss" scoped>
</style>